import React, { useEffect, useState} from "react";
import { useForm, NestedValue } from 'react-hook-form';
import { makeStyles } from '@material-ui/core/styles';
import { Box, Grid, Card, IconButton, Typography, CardHeader, colors, SvgIcon, Dialog, DialogActions, DialogContentText, DialogTitle, DialogContent, TextField, Toolbar, AppBar, Paper, Icon, Link, InputAdornment } from "@material-ui/core";
import CardContent from '@material-ui/core/CardContent';
import moment from 'moment';
import { Button, Tooltip } from '@material-ui/core';
import { AccountCircle, Close, Delete, Edit, Label, Menu, SettingsPowerRounded, SettingsRemoteRounded } from "@material-ui/icons";
import './containerCard.css';
import { TrashMile, TrackMile, DocumentMile, LocationMile } from "../../../assets/svg";
import { green, red } from '@material-ui/core/colors';
import { ButtonToolbar } from "react-bootstrap";
import { Autocomplete, TimelineDot } from "@material-ui/lab";
import AssignTruckAndDriverService from "../../../service/AssignTruckAndDriverService";
import RefVehicleTypeService from "../../../service/RefVehicleTypeService";
import RequestContainerService from "../../../service/RequestContainerService";
import TransporterService from "../../../service/TransporterService";
import { resolveModuleName } from "typescript";

// type DeleteState = (value: any) => boolean;
// onDelete?:DeleteState,
type item = {
    label: string;
    value: string;
}
interface ContainerParam {
    boxNumber?: number,
    declaration?: string,
    consigneeName?: string,
    dateAndTime?: string,
    contactPerson?: string,
    contactNumber?: string,
    fcl_truckNumber?: string,
    mt_truckNumber?: string,
    truckType?: string,
    tokenInNumber?: string,
    timeInSlot?: string,
    tokenOutNumber?: string,
    timeOutSlot?: string,
    pickUpLocation?: string,
    dropAddress?: string,
    dropZone?: string,
    containerNumber?: string,
    status?: any,
    order?:string,
    getAssignTruckState?: boolean;
    onDelete?(value?: any): boolean,
    assignTruck?(e?: boolean): boolean,
    onEdit?(value: any): void,
    openTrackClick?(): void
    deliveredClick?(e?: any): void
    fclOutClick?(e?: any): void
    mtInClick?(e?: any): void
    onContactPersonChange?(e?: any): string
    onContactNumberChange?(e?: any): string
    onDropLocationChange?(e?: any): string
    updateChange?(ctn?: any): any
    getEditContainer?(ctn?: any): any
    downloadDoc?(dwn?:any): any
}

const optionTrucks: item[] = [];
const optionTruckTypes: item[] = [];
const optionDriverNames: item[] = [];
const useStyles = makeStyles(theme => ({

    root: {
        flexGrow: 1,
    },
    title: {
        fontSize: 14,
        marginLeft: theme.spacing(2),
        flex: 1,
    },
    appBar: {
        position: 'relative',
    },
    pos: {
        marginBottom: 12,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    margin: {
        margin: theme.spacing(1),
    },
}));

const ContainerCard: React.FC<ContainerParam> = (ContainerParam) => {
    const classes = useStyles();
    const [openDelete, setOpenDelete] = useState(false);
    const [openEdit, setOpenEdit] = useState(false);
    const [assigned, setAssigned] = useState(false);
    const [truckNumber, setTruckNumber] = useState<any | null>('');
    const [truckNumberValue, setTruckNumberValue] = useState('');
    const [truckType, setTruckType] = useState<any | null>('');
    const [truckTypeValue, setTruckTypeValue] = useState('');
    const [truckTypes, setTruckTypes] = useState([]);
    const [truckNumbers, setTruckNumbers] = useState([]);

    const [driverNames, setDriverNames] = useState([]);
    const [driverName, setDriverName] = useState<any | null>('');
    const [driverNameValue, setDriverNameValue] = useState('');
    const [mtIn, setMtIn] = useState(false);
    const [activeMtIn, setActiveMtIn] = useState("");
    const [activeFclOut, setActiveFclOut] = useState("active");
    const [contactNumber, setContactNumber] = useState();
    const [contactPerson, setContactPerson] = useState();
    const [dropLocation, setDropLocation] = useState();
    const [err, setErr] = useState(false);

    const handleDeleteOpen = () => {
        setOpenDelete(true);
    };

    const handleDeleteClose = () => {
        setOpenDelete(false);
    };
    const handleDeleteYesClick = (deleteState: boolean) => {
        if (ContainerParam.onDelete) {
            setOpenDelete(false);
        }

    };

    const assignTruckClick = (next: any) => {
        setAssigned(true);
        if (typeof next == "function")
            next(true);
    }

    const handleDropLocation = (e: any) => {
        setDropLocation(e.target.value);
        if ( e.target.value.length === 0 )
            setErr(true);
        else setErr(false);
    };

    const handleContactPerson = (e: any) => {
        setContactPerson(e.target.value);
    };

    const handleContactNumber = (e: any) => {
        setContactNumber(e.target.value);
    };


    useEffect(() => {
        getTruckNumber();
        getTruckType();
        getDriverNames();
    }, [optionDriverNames, optionTruckTypes, optionTrucks]);

    const getTruckNumber = async () => {
        if (optionTrucks.length > 0) {
            let data = optionTrucks;
            setTruckNumbers(data);
        } else {
            let res: any[] = await AssignTruckAndDriverService.fetchTrucksForUserTypes()
            console.error("hahah ", JSON.stringify(res))
            if (res && Array.isArray(res)) {
                optionTrucks.push(...res); // set response in cache;
                console.log("TRUCK NUMBER ", res);
                setTruckNumbers(res);
            } else {
                setTruckNumbers([]);

            }
        }

    }
    const getTruckType = async () => {
        if (optionTruckTypes.length > 0) {
            setTruckTypes(optionTruckTypes);
        } else {
            let res: any[] = await RefVehicleTypeService.fetchVehicleType()
            optionTruckTypes.push(...res); // set response in cache;
            setTruckTypes(res);
        }
    }
    const getDriverNames = async () => {
        if (optionDriverNames.length > 0) {
            setDriverNames(optionDriverNames);
        } else {
            let res: any[] = await TransporterService.fetchDriverItems()
            if (res && Array.isArray(res)) {
                optionDriverNames.push(...res); // set response in cache;
                setDriverNames(res);
            } else {
                setDriverNames([])
            }


        }
    }
    const handleEditOpen = (next: any) => {
        setOpenEdit(true);
        setAssigned(false);
        if (typeof next == "function")
            next(false);
    };

    const handleEditClose = (next: any) => {
        setOpenEdit(false);
        setAssigned(false);
        if (typeof next == "function")
            next(false);
    };
    const handlerMtInClick = () => {
        setMtIn(true);
        setActiveFclOut("");
        setActiveMtIn("active");
    };

    const handlerFclOutClick = () => {
        setMtIn(false);
        setActiveFclOut("active");
        setActiveMtIn("");
    };

    const updateContainer = async (ctn: any) => {
        if (ctn) {
            let res: any = await RequestContainerService.updateContainerDetails(ctn);
            return res;

        }
    }

    const onEtokenDownload=(jobRefNo:any,containerNumber:any,tokenType:any)=>
    {
        console.log("mtIn",mtIn);
        console.log("ContainerParam.tokenOutNumber",ContainerParam.tokenOutNumber);
        console.log("ContainerParam.tokenInNumber",ContainerParam.tokenInNumber);
  
        RequestContainerService.fetchEtoken(jobRefNo,containerNumber,tokenType)
          .then((response) => {
            if (response.isAxiosError) throw new Error(response.status);
            else {
  
              const linkSource = `data:${response.data.dataItems[0].filetype};base64,${response.data.dataItems[0].fileContent}`;
              const downloadLink = document.createElement("a");
  
              downloadLink.href = linkSource;
              downloadLink.download = response.data.dataItems[0].fileName;
              downloadLink.target = "_blank";
              // alert(downloadLink);
             downloadLink.click();
            }
          })
  
          .catch(() => {
            console.log("error");
          });
  
    }

    const onFileDownload = (dwn:any) => {
        if (dwn && typeof dwn == "function") {
            let value:any = dwn();
            console.log("value to download ", value);
       
        RequestContainerService.fetchPod(value?.jobNo, value?.containerNo)
            .then((response) => {
                if (response.isAxiosError) throw new Error(response.status);
                else {
                    const linkSource = `data:${response.data.dataItems[0].filetype};base64,${response.data.dataItems[0].fileContent}`;
                    const downloadLink = document.createElement("a");

                    downloadLink.href = linkSource;
                    downloadLink.download = response.data.dataItems[0].fileName;
                    downloadLink.target = "_blank";
                    // alert(downloadLink);
                    downloadLink.click();
                }
            })

            .catch(() => {
                console.log("error");
            });
    }
    };
    const save = async (getCtn?: any, next?: any) => {
        let res: any
        if (getCtn && typeof getCtn == "function") {
            let ctn = getCtn();
            console.log("Container from parent ", ctn)
            if (ctn) {
                ctn.dropAddress = dropLocation ?? ContainerParam.dropAddress
                ctn.consigneeContactNumber = contactNumber ?? ContainerParam.contactNumber
                ctn.consigneeContactName = contactPerson ?? ContainerParam.contactPerson

                res = await updateContainer(ctn)
                if (res && res.status === "success") {
                    console.log("Update response successfull ", res)
                    setOpenEdit(false);
                    if (next && typeof next == "function")
                        return next(ctn);
                } else {
                    console.log("Update response fail ", res)
                }

            }
        }
    };

    // const save = async (ctn?: any) => {
    //     let res: any

    //     if (ctn &&  typeof ctn == "function") {
    //         let container = ctn()
    //         // container.container_number="2355655"
    //         container.dropAddress=dropLocation??ContainerParam.dropAddress
    //         container.consigneeContactNumber=contactNumber??ContainerParam.contactNumber
    //         container.consigneeContactName=contactPerson??ContainerParam.contactPerson
    //         console.log("Container to update  ", container)
    //         res = await updateContainer(container)
    //         if(res && res.status==="success"){
    //             console.log("Update response successfull ", res)
    //             setOpenEdit(false);
    //         }else{
    //             console.log("Update response fail ", res)
    //         }

    //     }

    // };

    return (
        <>
            <Card className="myCard" variant="elevation">
                {/* <Box className="boxOnCard">{ContainerParam?.boxNumber}</Box> */}
                <CardContent>
                    <Grid item container xs={12} sm spacing={1}>
                        <Grid item xs={12} sm={1} md={1}>
                            <Box p={2} className="boxOnCard" component="span" >
                                <Typography variant="caption">{ContainerParam?.boxNumber}</Typography>
                            </Box>
                        </Grid>
                        <Grid item xs={12} sm={3} md={3}>
                            <Typography variant="subtitle2">{ContainerParam?.containerNumber}</Typography>
                        </Grid>
                        <Grid item container sm={8} md={8} justify="flex-end" alignItems="center">

                            {
                                ContainerParam?.status && ContainerParam?.status?.code === "PEND" ?
                                    <Typography variant="button" className="mile-red mile-font" >
                                        <span><i className="mile-dot">
                                            &bull;</i> {ContainerParam.status?.name ?? "None"}</span>
                                        <>&thinsp;
                                    <Icon className="svg-red" aria-label="deletes" onClick={handleDeleteOpen} >
                                                <SvgIcon component={TrashMile} className="svg-red" />
                                            </Icon>

                                            <Icon className="svg-blue" aria-label="edit" onClick={handleEditOpen}>
                                                {/* <SvgIcon component={Edit} className="svg-blue" /> */}
                                                <Edit className="svg-blue"></Edit>
                                            </Icon>
                                        </>
                                    </Typography>

                                    : (ContainerParam?.status?.code === "COMPL" || ContainerParam?.status?.code === "MT_DEL" || ContainerParam?.status?.code === "CONF" || ContainerParam?.status?.code === "MTTOKASGN") ?
                                        <>
                                            <Typography variant="button" className="mile-green mile-font" > <span><i className="mile-dot">&bull;</i> {ContainerParam.status?.name ?? "None"}</span> </Typography>
                                            {ContainerParam?.status && ContainerParam?.status?.code === "COMPL" &&
                                                <Box className="settleBox">Settled</Box>}
                                        </>
                                        : (ContainerParam?.status?.code === "FCL_DEL") ?
                                            <>
                                                <Typography variant="button" className="mile-purple mile-font " ><span ><i className="mile-dot">&bull;</i> {ContainerParam.status?.name ?? "None"}</span> </Typography>

                                            </>

                                            : (ContainerParam?.status?.code === "PTOK" || ContainerParam?.status?.code === "INPRO") ?

                                                <Typography variant="button" align="justify" className="mile-orange mile-font " >
                                                    <span>
                                                        <i className="mile-dot">&bull;</i>
                                                        {` ${ContainerParam.status?.name ?? "None"}`}
                                                    </span>
                                                    {ContainerParam?.status?.code === "INPRO" &&
                                                        <Link className="mile-red mile-cursur " noWrap onClick={(e: any) => { ContainerParam?.deliveredClick(e) }} > {` Delivered?`}</Link>}
                                                </Typography>

                                                :
                                                <Typography variant="button" className="mile-red mile-font" >
                                                    <span><i className="mile-dot">&bull;</i> {ContainerParam.status?.name ?? "None"}</span>
                                                </Typography>

                            }
                            {/* {ContainerParam?.status && ContainerParam?.status?.code === "PEND" &&
                                <>&thinsp;
                                    <Icon aria-label="deletes" onClick={handleDeleteOpen} >
                                        <SvgIcon component={TrashMile} className="svg-red" />
                                    </Icon>
                                    <Icon aria-label="edit" onClick={handleEditOpen}>
                                        <SvgIcon component={Edit} className="svg-blue" />
                                    </Icon>
                                </>
                            } */}


                        </Grid>
                    </Grid>
                    <Grid item container xs={12} spacing={1}>
                        <Grid item xs={12} sm={3} md={3}   >
                            <Typography variant="body1" noWrap>
                                <SvgIcon variant="body1" fontSize="small" component={LocationMile} style={{ color: "#000" }} ></SvgIcon>
                                {`${ContainerParam?.pickUpLocation}`}&thinsp;
                            <span style={{ fontSize: 35, color: "#000" }}>&bull;</span><span style={{ fontSize: 30, color: "#efefef" }}>-&thinsp;-&thinsp;-</span><span style={{ fontSize: 35, color: "#000" }}>&bull;</span>
                            
                           <Tooltip title={`${ContainerParam?.dropZone}/${ContainerParam?.dropAddress}`}><IconButton>  <SvgIcon variant="body1" fontSize="small" component={LocationMile} style={{ color: "#000" }} ></SvgIcon></IconButton></Tooltip>
                            &thinsp;{`${ContainerParam?.dropZone}/${ContainerParam?.dropAddress}`}
                            </Typography>
                        </Grid>
                        <Grid item xs={6} sm={2} md={2} >
                            <Typography variant="subtitle1">Declaration#</Typography>
                            <Typography variant="subtitle2">{ContainerParam?.declaration}</Typography>
                        </Grid>
                        <Grid item xs={6} sm={2} md={2}>
                            <Typography variant="subtitle1">Consignee  Name</Typography>
                            <Typography variant="subtitle2">{ContainerParam?.consigneeName}</Typography>
                        </Grid>
                        <Grid item xs={6} sm={2} md={2}>
                            <Typography variant="subtitle1">Date and time </Typography>
                            <Typography variant="subtitle2">{ContainerParam?.dateAndTime ? moment(ContainerParam?.dateAndTime).format("DD-MM-YYYY hh:mm") : ""}</Typography>
                        </Grid>
                        <Grid item xs={6} sm={2} md={2} >
                            <Typography variant="subtitle1">Contact person </Typography>
                            <Typography variant="subtitle2">{ContainerParam?.contactPerson}</Typography>
                        </Grid>
                        <Grid item xs={6} sm={2} md={1} >
                            {ContainerParam?.status && ContainerParam?.status?.code !== "INPRO" && ContainerParam?.status?.code !== "PEND" && ContainerParam?.status?.code !== "CONF" &&
                                <Icon aria-label="track" className="mile-cursur" onClick={()=>{onFileDownload(ContainerParam.downloadDoc)}} >
                                    <SvgIcon titleAccess={"POD"} component={DocumentMile} className="svg-doc" />
                                </Icon>
                            }
                        </Grid>
                    </Grid>
                    <Grid item container xs={12} sm spacing={1}>
                        <Grid item xs={6} sm={2} md={2}>
                            <Typography variant="subtitle1">Contact Number </Typography>
                            <Typography variant="subtitle2">{ContainerParam?.contactNumber}</Typography>
                        </Grid>
                        <Grid item xs={6} sm={1} md={1} >
                        </Grid>
                        <Grid item xs={6} sm={2} md={2}>
                            <Typography variant="subtitle1">Truck Number</Typography>
                            {mtIn ?
                            <Typography variant="subtitle2">{ContainerParam.mt_truckNumber ?? "Not Assigned yet"}</Typography>:
                            <Typography variant="subtitle2">{ContainerParam.fcl_truckNumber ?? "Not Assigned yet"}</Typography>
}
                        </Grid>
                        <Grid item xs={6} sm={2} md={2}>
                            <Typography variant="subtitle1">Truck Type</Typography>
                            <Typography variant="subtitle2">{ContainerParam.truckType ?? "None"}</Typography>
                        </Grid>

                        {mtIn ?
                            <>
                                <Grid item xs={6} sm={2} md={2} >
                                    <Typography variant="subtitle1">Token Number </Typography>
                                    <Typography variant="subtitle2">{ContainerParam.tokenInNumber ?? "Not created"}</Typography>
                                </Grid>
                                <Grid item xs={6} sm={2} md={2} >
                                    <Typography variant="subtitle1">Time Slot </Typography>
                                    <Typography variant="subtitle2">{ContainerParam.timeInSlot ?? "None"}</Typography>
                                </Grid>
                            </> :
                            <>
                                <Grid item xs={6} sm={2} md={2} >
                                    <Typography variant="subtitle1">Token Number </Typography>
                                    <Typography variant="subtitle2">{ContainerParam.tokenOutNumber ?? "Not created"}</Typography>
                                </Grid>
                                <Grid item xs={6} sm={2} md={2} >
                                    <Typography variant="subtitle1">Time Slot </Typography>
                                    <Typography variant="subtitle2">{ContainerParam?.timeOutSlot ?? "None"}</Typography>
                                </Grid>
                            </>
                        }

                        <Grid item xs={3} sm={1} md={1} >
                            <Icon aria-label="track" onClick={ContainerParam.openTrackClick} >
                                <SvgIcon component={TrackMile} className="svg-red" />
                            </Icon>
                        </Grid>
                        </Grid>
                       
                        <Grid item container xs={12} sm spacing={1}>
                           {ContainerParam.tokenInNumber  && mtIn  &&
                    
                        <>
                          <Grid item xs={3} sm={1} md={1} > <img src="./download_doc.svg" /><Link onClick={() =>onEtokenDownload(ContainerParam.order, ContainerParam.containerNumber,"MT IN")}>eToken</Link></Grid></>}
                        
                          {ContainerParam.tokenOutNumber  && !mtIn &&
                        
                        <><Grid item xs={3} sm={1} md={1} > <img src="./download_doc.svg" /><Link onClick={() =>onEtokenDownload(ContainerParam.order, ContainerParam.containerNumber,"FCL OUT")}>eToken</Link></Grid>
                       </>
}

                      
                    </Grid>
                  
                    <Grid container xs={12} direction="row" justify="flex-end" alignItems="flex-end">

                        <>
                            {(ContainerParam?.status?.code === "COMPL" || ContainerParam?.status?.code === "MT_DEL" || ContainerParam?.status?.code === "MTTOKASGN") &&
                                <Box component="span" className={`${activeMtIn} empty_in`} onClick={() => ContainerParam.mtInClick(handlerMtInClick())}>
                                    <Typography variant="caption">EMPTY  IN</Typography>
                                </Box>}
                            {(ContainerParam?.status?.code === "INPRO" || ContainerParam?.status?.code === "PMTTOK" || ContainerParam?.status?.code === "CONF" || ContainerParam?.status?.code === "FCL_DEL" || ContainerParam?.status?.code === "COMPL" || ContainerParam?.status?.code === "MT_DEL" || ContainerParam?.status?.code === "MTTOKASGN" || ContainerParam?.status?.code === "FCL_DEL" || ContainerParam?.status?.code === "COMPL" || ContainerParam?.status?.code === "PODUPL") &&
                                <Box component="span" className={`${activeFclOut} fcl_out`} onClick={() => ContainerParam.fclOutClick(handlerFclOutClick())}>
                                    <Typography variant="caption">FCL OUT</Typography>
                                </Box>}
                        </>

                    </Grid>
                </CardContent>
                {/*   <CardActions>
                            <Button size="small">Learn More</Button>
                        </CardActions> */}
            </Card>

            <div>
                <Dialog
                    open={openDelete}
                    onClose={handleDeleteClose}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                >
                    <DialogTitle id="alert-dialog-title" style={{ backgroundColor: '#0E1B3D' }}>{"Attention !"}</DialogTitle>
                    <DialogContent>
                        <DialogContentText id="alert-dialog-description">
                            <Typography variant="body1" align="center">Are you sure to cancel the booking for this Item</Typography>
                            <Typography variant="body2" align="center" > Please view  our <span color="error">Cancellation policy !</span></Typography>

                        </DialogContentText>
                    </DialogContent>
                    {/* ContainerParam.onDelete(e) */}
                    <DialogActions style={{ alignSelf: "center" }}>
                        <Button onClick={() => ContainerParam.onDelete()}  style={{
                                    background: "#4CAB5B",
                                    color: "#fff",
                                    textTransform: "none",
                               
                                }}> Yes  </Button>

                        <Button onClick={handleDeleteClose}  style={{
                                    background: "#dc4e4e",
                                    color: "#fff",
                                    textTransform: "none",
                               
                                }} autoFocus>
                            No
          </Button>
                    </DialogActions>
                </Dialog>

                <Dialog maxWidth="md" fullWidth={true} open={openEdit} onClose={() => handleEditClose(ContainerParam.assignTruck)} aria-labelledby="form-dialog-title">
                    <AppBar color="transparent" className={classes.appBar}>
                        <Toolbar>
                            <Box color="text.primary" className="boxOnDialog" component="span">
                                <Typography variant="caption">{ContainerParam?.boxNumber ?? 1}</Typography>
                            </Box>


                            <Typography variant="h3" className={classes.title}>
                                {ContainerParam?.containerNumber.toUpperCase()}
                            </Typography>
                            <Typography variant="subtitle1" >
                                {/* {ContainerParam?.status?.name} */} Truck not assigned
                            </Typography>
                            {/* <Button color="primary" disabled={assigned} size="small" onClick={() => { assignTruckClick(ContainerParam.assignTruck) }}>Assign Truck ?</Button> */}
                        </Toolbar>
                    </AppBar>
                    {/* <DialogTitle id="form-dialog-title">Subscribe</DialogTitle> */}
                    <DialogContent>
                        <DialogContentText> {/* To assign truck click assign Truck . */} </DialogContentText>
                        {/* <TextField autoFocus margin="dense" id="name" label="Email Address" type="email" fullWidth /> */}
                        <Grid container xs={12} sm spacing={1}>
                            <Grid item xs={12} sm={6} md={6} >
                                <Grid container xs={12} alignItems="flex-end">
                                    <Grid item>
                                        <SvgIcon variant="body1" fontSize="small" component={LocationMile} style={{ color: "#000" }} />&thinsp;
                                            {ContainerParam?.pickUpLocation}
                                        <span style={{ fontSize: 35, color: "#000" }}>&bull;</span>
                                        <span style={{ fontSize: 30, color: "#efefef" }}>-&thinsp;-&thinsp;-</span>
                                        <span style={{ fontSize: 35, color: "#000" }}>&bull;</span>
                                        <SvgIcon variant="body1" fontSize="small" component={LocationMile} style={{ color: "#000" }} />
                                    </Grid>
                                    <Grid item>
                                        <TextField value={dropLocation} defaultValue={ContainerParam?.dropAddress} error={err} helperText={err?"Drop Location is required":""} id="dropLocation" onChange={e => { ContainerParam.onDropLocationChange(handleDropLocation(e)) }} />
                                    </Grid>
                                </Grid>

                            </Grid>
                            <Grid item xs={6} sm={2} md={2} >
                                <Typography variant="subtitle1">Declaration#</Typography>
                                <Typography variant="subtitle2">{ContainerParam?.declaration}</Typography>
                            </Grid>
                            <Grid item xs={6} sm={2} md={2}>
                                <Typography variant="subtitle1">Conseignee Name</Typography>
                                <Typography variant="subtitle2">{ContainerParam?.consigneeName}</Typography>
                            </Grid>
                            <Grid item xs={6} sm={2} md={2}>
                                <Typography variant="subtitle1">Token Number </Typography>
                                <Typography variant="subtitle2">{ContainerParam?.tokenOutNumber ?? "Not Created "}</Typography>
                            </Grid>
                        </Grid>
                     
                    </DialogContent>
                    <DialogActions>
                        {/*  ContainerParam.assignTruck should be removed in handEditClose */}
                        <Button onClick={() => handleEditClose(ContainerParam.assignTruck)} color="primary"> Cancel </Button>
                        {/* <Button onClick={(e) => ContainerParam.updateChange(save(e))} color="primary"> Save  </Button> */}
                        <Button onClick={() => { if (!err) { save(ContainerParam.updateChange, ContainerParam.getEditContainer) }}} color="primary"> Save  </Button>
                    </DialogActions>
                </Dialog>
            </div>
        </>

    )

}

export default ContainerCard;