import React, { useEffect, useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import { Divider, Grid, IconButton, InputBase, MenuItem, Paper, TextField } from "@material-ui/core";

import { Directions, Search } from "@material-ui/icons";
import './containerCard.css';
import theme from "../../common/theme/theme";


const useStyles = makeStyles(() => ({
    root: {
        padding: '2px 4px',
        display: 'flex',
        alignItems: 'center',
        width: 700,
        flexGrow: 1,
        marginTop:15
    },
    input: {
        marginLeft: theme.spacing(1),
        flex: 1,
    },
    iconButton: {
        padding: 10,
    },
    divider: {
        height: 28,
        margin: 4,
    },
}));

interface SearchInputParam {
    placeholder?: string,
    defaultType?: string,
    defaultValue?: string,
    searchClick?(value: any): void,
    changeType?(value?: any): string,
    changeValue?(value?: any): string,
    optionValues?:any[]

}

// const types = [
//     {
//         value: 'DECLARATION',
//         label: 'By Declaration Number',
//     },

//     {
//         value: 'BOOKING',
//         label: 'By Booking Number',
//     },
//     {
//         value: 'CONTAINER',
//         label: 'By Container Number',
//     }
// ];


const SearchInput: React.FC<SearchInputParam> = (SearchInputParam) => {
    const classes = useStyles();
    const [search, setSearch] = useState();
    const [type, setType] = useState();
    const [typeOptions, setTypeOptions] = useState(SearchInputParam.optionValues);
    const handleInputValueChange = (event: any) => {
        setSearch(event.target.value);
        return event.target.value;
    }

    const handleTypeChange = (event: any) => {
        setType(event.target.value);
        return event.target.value;
    }
    // useEffect(() => {

    //     console.info("BEFORE ", JSON.stringify(type))

    // }, [])
    // const handleTypeChange = (event?: any) => {
    //     SearchInputParam.getType(event.target.value);
    //     setType(event.target.value);
    //     console.log("INFO ++++ ",type)
    // };

    return (
        <>

            <Paper component="form" className={classes.root}>
          
            <InputBase  value={search??SearchInputParam.defaultValue} className={classes.input} onChange={(e) => SearchInputParam.changeValue(handleInputValueChange(e))} placeholder={`Search by ${type ?? SearchInputParam.defaultType}`} inputProps={{ 'aria-label': `Search by ${type ?? SearchInputParam.defaultType}` }} />
                
                {/* <InputBase value={search??SearchInputParam.defaultValue} className={classes.input} onChange={(e) => handleSearchInput(e, SearchInputParam?.getValue(e.target.value))} placeholder={`Search by ${type ?? SearchInputParam.defaultType}`} inputProps={{ 'aria-label': `Search by ${type ?? SearchInputParam.defaultType}` }} /> */}
                {/* <InputBase value={search??SearchInputParam.defaultValue} className={classes.input} onChange={(e) => handleSearchInput(e,SearchInputParam.getType(e.target.value))} placeholder={`Search by ${type ?? SearchInputParam.defaultType}`} inputProps={{ 'aria-label': `Search by ${type ?? SearchInputParam.defaultType}` }} /> */}
                <IconButton onClick={() => SearchInputParam.searchClick({"serviceType":type??SearchInputParam.defaultType,"serviceNumber":search})} className={classes.iconButton} aria-label="search">
                    <Search />
                </IconButton>
                <Divider className={classes.divider} orientation="vertical" />
                {/*   <IconButton color="primary" className={classes.iconButton} aria-label="directions">
                    <Directions />
                </IconButton> */}
                <TextField
                    id="standard-select-currency"
                    select
                    value={type??SearchInputParam.defaultType}
                    onChange={(e) => SearchInputParam.changeType(handleTypeChange(e))}
                    variant="outlined"
                    size="small"
                >
                    {typeOptions.map((option, i) => (
                        <MenuItem key={i} value={option.value}>
                            {option.label}
                        </MenuItem>
                    ))}
                </TextField>
            </Paper>
        </>

    )

}

export default SearchInput;