import React, { useState, Fragment, useEffect } from "react";

import { makeStyles, withStyles } from "@material-ui/core/styles";

import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";

import StepConnector from "@material-ui/core/StepConnector";

import Typography from "@material-ui/core/Typography";
import { Rowing } from "@material-ui/icons";
import { InputLabel } from "@material-ui/core";


const TestConnector = withStyles({
  alternativeLabel: {
    top: 22,
  },
  active: {
    "& $line": {
      borderColor: "#FF0000",
    },
  },
  completed: {
    "& $line": {
      borderColor: "#FF0000",
    },
  },
  line: {
    borderColor: "#eaeaf0",
    borderTopWidth: 3,
    borderRadius: 1,
  },
})(StepConnector);

const useStyles = makeStyles((theme) => ({
  root: {
    width: "70%",
  },
  button: {
    marginRight: theme.spacing(1),
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  icon: {
    color: "green !important",
  },
}));

// function getSteps() {
//   return ["Booked on", "Truck Assigned", "Started job","Delivered to wareHouse","empty container back"];
// }

export default function Track(props) {
  console.log("****",props);
  
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(2);
  const steps = props.steps;

  useEffect(() => {

    if (props.tab === "Pending")
      setActiveStep(1);
    if (props.tab === "Confirmed")
      setActiveStep(2);
      if (props.tab === "In Progress")
      setActiveStep(3);
      if (props.tab === "Delivered")
      setActiveStep(4);
      if (props.tab === "Completed")
      setActiveStep(5);
  },[])

  

 

  function getStepContent(activeStep) {

    switch (activeStep) {
      case 1:
        return (
        <></>
        );
      case 2:
        return (
          <></>
        );
      case 3:
        return (
          <></>
        );
        case 4:
        return (
          <></>
        );
        case 5:
          return (
            <></>
          );
    }
  }

  return (
    <div className={classes.root}>
      <Stepper orientation="vertical"
        
        activeStep={activeStep}
        connector={<TestConnector />}
      >
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel
              StepIconProps={{
                classes: {
                  active: classes.icon,
                  completed: classes.icon,
                },
              }}
            >
            <InputLabel> {label}</InputLabel>
              
            </StepLabel>
          </Step>
        ))}
      </Stepper>

      <div>
        <Typography component={"div"} className={classes.instructions}>
          {getStepContent(activeStep)}
        </Typography>
      </div>
    </div>
  );
}
