import React, { useState, useEffect, useRef } from "react";
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import { withStyles, StepConnector, Step, StepLabel, Stepper, StepContent, IconButton, SwipeableDrawer, MenuItem, Select, Dialog, DialogActions, DialogContentText, DialogContent, Paper, Box, Button, Grid, Typography } from "@material-ui/core";
import { Close, ArrowLeftOutlined, Edit, ArrowBackOutlined } from "@material-ui/icons";
import DateFnsUtils from '@date-io/date-fns';
import { KeyboardDateTimePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";
import Timeline from '@material-ui/lab/Timeline';
import TimelineItem from '@material-ui/lab/TimelineItem';
import TimelineSeparator from '@material-ui/lab/TimelineSeparator';
import TimelineConnector from '@material-ui/lab/TimelineConnector';
import TimelineContent from '@material-ui/lab/TimelineContent';
import TimelineDot from '@material-ui/lab/TimelineDot';
import moment from 'moment'
import BookingCard from "../../lib/components/appComponent/BookingCard";
import SearchInput from "../../lib/components/appComponent/SearchInput";
import ContainerCard from "../../lib/components/appComponent/ContainerCard";
import BookingService from '../../service/BookingService'
import '../../lib/components/appComponent/containerCard.css';
import TruckTypeCard from "../../lib/components/appComponent/TruckTypeCard";
import Track from "../../components/stepper/Track";
import RefVehicleTypeService from "../../service/RefVehicleTypeService";
import UploadDocumentPopup from "../../components/transporter/UploadDocumentPopup";
import * as utilis from "../../utils/utilis";
import '../../../src/assets/styles.css';
import { set } from "date-fns";
import CloseIcon from "@material-ui/icons/Close";
import { InputLabel } from "@material-ui/core";
import Divider from "@material-ui/core/Divider";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(2),
        margin: 'auto',
        maxWidth: 1200,
    },
    actionsContainer: {
        marginBottom: theme.spacing(2),
    },
    table: {
        minWidth: 650,
    },
    list: {
        width: 350,
    },
    instructions: {
        marginTop: theme.spacing(1),
        marginBottom: theme.spacing(1),
    },
    icon: {
        color: "green !important",
    },

}));
const TestConnector = withStyles({
    alternativeLabel: {
        top: 22,
    },
    active: {
        "& $line": {
            borderColor: "#FF0000",
        },
    },
    completed: {
        "& $line": {
            borderColor: "#FF0000",
        },
    },
    line: {
        borderColor: "#eaeaf0",
        borderTopWidth: 3,
        borderRadius: 1,
    },
})(StepConnector);
const intervals = [
    { label: "30 min", value: "00:30" },
    { label: "1 h", value: "01:00" },
    { label: "2 h", value: "02:00" },
    { label: "3 h", value: "03:00" },
    { label: "4 h", value: "04:00" },

]

const serviceOptionTypes = [
    {
        value: 'DECLARATION',
        label: 'By Declaration Number',
    },

    {
        value: 'BOOKING',
        label: 'By Booking Number',
    },
    {
        value: 'CONTAINER',
        label: 'By Container Number',
    }
];
const optionTruckTypes = [];
const popUpParams = { uploadType: "POD", referenceNumber: "", message: "HHHJHHHHGG", dpwTransactionId: "", boeNumber: "BOE144", status: "" }

export default function SearchDetails(propsValue) {
    const classes = useStyles();
    const [inputType, setInputType] = useState('');
    const [searchValue, setSearchValue] = useState('');

    const [success, setSuccess] = useState(false);
    const [boeDetails, setBoeDetails] = useState();
    const [booking, setBooking] = useState();
    const [containers, setContainers] = useState();
    // const [truckNumbers, setTruckNumbers] = useState([]);
    const [openEdit, setOpenEdit] = useState(false);
    const [openDeliveredDialog, setOpenDeliveredDialog] = useState(false);
    const [truckTypes, setTruckTypes] = useState([]);
    const [selectedTruckTypes, setSelectedTruckTypes] = useState({});
    const [selectedDate, setSelectedDate] = useState()
    const [interval, setInterval] = useState();
    const [propsHistoryValue, setPropsHistoryValue] = useState({});
    const [openTrack, setOpenTrack] = useState(false);
    const [trackDetails, setTrackDetails] = useState();
    const [uploadParam, setUploadParam] = useState();
    const [activeStep, setActiveStep] = useState(10);
    let dateRef = useRef();
    let selectIntervalRef = useRef();


    const handleOpenEdit = (req, toDo) => {
        setSelectedDate(moment(req[0]?.dateAndTime, "DD-MM-YYYY HH:mm").format("YYYY-MM-DDTHH:mm"));
        setInterval(req[0]?.interval)
        setSelectedTruckTypes({ ...selectedTruckTypes, "request": req ?? [], "action": toDo })
        setOpenEdit(true)
    }
    const handleEditClose = () => {
        if (dateRef.current) {
            setSelectedDate(moment(dateRef.current, "DD-MM-YYYY HH:mm").format("YYYY-MM-DDTHH:mm"));
        }
        if (selectIntervalRef.current) {
            setInterval(selectIntervalRef.current);
        }
        setOpenEdit(false)
    }

    const handleOpenDeliveredDialog = (ctn) => {
        setOpenDeliveredDialog(true)
        let msg = `Please note: Document will automatically approved`
        setUploadParam({ ...uploadParam, uploadType: "POD", referenceNumber: ctn?.container_number, customMessage: msg, dpwTransactionId: ctn?.dpwTransactionId, boeNumber: ctn?.boeNumber })
    }
    const handleCloseDeliveredDialog = () => {
        setOpenDeliveredDialog(false)
        if (searchValue && inputType) {
            landClick(searchValue, inputType)
        }
    }


    function getSteps(tracks,bookingDate) {
        let steps = [];
       
        steps.push(`Booked on ${bookingDate}`);
        if (Array.isArray(tracks)) {
            tracks.forEach((item) => {
                if (item) {
                    console.log("item ", item)
                   
                    if (item.status === 'PTOK') {
                        steps.push(`Truck assigned on  ${item?.actionDateAndTime}`)
                    }
                    if (item?.status === 'INPRO')
                        steps.push(`job started on  ${item?.actionDateAndTime}`);
                    if (item?.status === "PODUPL")
                        steps.push(`POD Submitted on ${item?.actionDateAndTime}`)
                    if (item?.status === "CONF")
                        steps.push(`Admin assigned token on  ${item?.actionDateAndTime}`)
                    if (item?.status === "FCL_DEL")
                        steps.push(`Delivered on  ${item?.actionDateAndTime}`)
                        if (item?.status === "COMPL")
                        steps.push(`Container back to yard on  ${item?.actionDateAndTime}`)
                }
            });
            // console.log("steps ", steps)

        }
        return steps;

    }



    const save = async () => {
        if (booking?.data?.referenceNumber && selectedTruckTypes.request) {
            let res = await BookingService.updateRequestTruckType(booking.data.referenceNumber, selectedTruckTypes.request);
            if (res && res?.status === "success") {
                booking.data.requestTruckType = [...selectedTruckTypes.request]
                setBooking({ ...booking, "data": booking.data })
                setOpenEdit(false)
            }
        }


    }


    const onIntervalChange = (event) => {
        let interval = event.target.value
        setInterval(interval)
        let firstValue = Object.assign({}, selectedTruckTypes.request[0]);
        firstValue.interval = interval;
        let varInterval = 0;
        let newList = [];
        newList.push(firstValue)
        for (let i = 0; i < selectedTruckTypes.request.length - 1; i++) {
            varInterval = varInterval + utilis.convertToMillSeconds(interval);
            let nexDate = getNextDateWithNumber(varInterval, firstValue.dateAndTime);
            let newValue = { ...firstValue, "dateAndTime": moment(nexDate, "DD/MM/YYYY HH:mm").format("DD-MM-YYYY HH:mm") };
            newList.push(newValue)
        }
        setSelectedTruckTypes({ ...selectedTruckTypes, "request": [...newList], "action": "editInterval" })
    }


    useEffect(() => {
        getTruckType()
        if (propsValue?.history?.location?.state?.search) {
            setPropsHistoryValue({ ...propsHistoryValue, "serviceNumber": propsValue?.history?.location?.state?.search?.serviceNumber, "serviceType": propsValue?.history?.location?.state?.search?.serviceType, "from": propsValue?.history?.location?.state?.from })
            setSearchValue(propsValue?.history?.location?.state?.search?.serviceNumber)
            setInputType(propsValue?.history?.location?.state?.search?.serviceType)

            //  if (propsValue?.history?.location?.state?.from == true) {
            if (propsValue?.history?.location?.state?.from == true) {
                doSeacrch(propsValue?.history?.location?.state?.search?.serviceNumber, propsValue?.history?.location?.state?.search?.serviceType)
            }
        }


    }, []);

    const getTruckType = async () => {
        if (optionTruckTypes.length > 0) {
            setTruckTypes(optionTruckTypes);
        } else {
            let res = await RefVehicleTypeService.fetchVehicleType()
            if (res && res?.length > 0) {
                optionTruckTypes.push(...res); // set response in cache;
                setTruckTypes(res);
            }
        }

    }

    const openDrawer = (open) => {

        setOpenTrack(open)
    }

    const closeDrawer = () => {
        setOpenTrack(false)
    }

    const onGetTrackList = (container, next) => {
       // console.log(JSON.stringify("VERITE ", container));
        console.log("cccccccccccccccccccc", container);
        setTrackDetails({ ...trackDetails, "containerNo": container?.container_number, "tracks": container?.containerTrackList ?? [],"bookingDate":container?.bookingDate })
        if (typeof next == "function")
            next = () => {

            };
    }


    const getNextDate = (interval, dateTime) => {
        let currentDate = moment(dateTime, "DD-MM-YYYY HH:mm").format("YYYY-MM-DDTHH:mm");
        let nextDateTime = utilis.getNextDateTime(utilis.convertToMillSeconds(interval), currentDate)
        return nextDateTime;
    }

    const getNextDateWithNumber = (interval, dateTime) => {
        let currentDate = moment(dateTime, "DD-MM-YYYY HH:mm").format("YYYY-MM-DDTHH:mm");
        let nextDateTime = utilis.getNextDateTime(interval, currentDate)
        return nextDateTime;
    }
    const addMore = () => {
        let lastItem = Object.assign({}, selectedTruckTypes?.request[selectedTruckTypes?.request?.length - 1]);

        let nextDate = getNextDate(lastItem?.interval, lastItem.dateAndTime)
        lastItem.dateAndTime = moment(nextDate, "DD/MM/YYYY HH:mm").format("DD-MM-YYYY HH:mm")
        setSelectedTruckTypes({ ...selectedTruckTypes, "request": [...selectedTruckTypes.request, lastItem], "action": "addMore" })

    }


    const updateContainerChange = (ctn) => {
        console.log("ctn from child  ", ctn)
        return ctn;
    };
    const updateParentBookingView = (key, updateCtn) => {
        let ctn = Object.assign({}, updateCtn);
        booking.data.containerList[key] = ctn
        setBooking({ ...booking, "data": { ...booking.data, "containerList": [...booking.data?.containerList] } })
    };
    const updateContainerView = (key, updateCtn) => {
        let ctn = Object.assign({}, updateCtn);
        containers.data[key] = ctn
        setContainers({ ...containers, "data": containers?.data })
    };
    const handleDownload = (ctn) => {
        if (ctn) {
            return { "jobNo": ctn?.order, "containerNo": ctn?.container_number }
        }
    };
    const handleDelete = async (ctn, key, service) => {
        let deleteData = {
            dpwTransactionId: ctn.dpwTransactionId,
            requestDetailsNumber: ctn.requestDetailsNumber,
            container_number: ctn.container_number,
            refStatus: { code: "CNCL" },
        };
        console.info(`Delete Data   ${JSON.stringify(deleteData)} with ${key} and ${service}`)

        let res = await BookingService.cancellationRequest(deleteData)
        if (res && res?.status === "success") {
            if (service === "booking") {
                delete booking.data[0]?.[key];
                await setBooking({ ...booking, "data": booking.data[0] })
            } else {
                delete containers?.data[key];
                await setContainers({ ...containers, "data": containers?.data })
            }
            return await false;
        } else {
            return await false;
        }
    }
    const onBookingViewClick = async (serviceNumber) => {
        await setBoeDetails();
        await setContainers()
        let res = await BookingService.getBookingByNumber(serviceNumber)
        if (res && res?.status === "success") {
            // await console.info("search +++++++ ", JSON.stringify(res?.data?.dataItems))
            await setSuccess(true)
            await setBooking({ ...booking, "data": res?.data?.dataItems[0] })
        }
        else {
            await setSuccess(false)
        }
    }

    const handleDateChange = (date) => {
        let firstValue = Object.assign({}, selectedTruckTypes.request[0]);
        dateRef.current = moment(date, "DD-MM-YYYY HH:mm").format("YYYY-MM-DDTHH:mm");
        setSelectedDate(date);
        firstValue.dateAndTime = moment(date).format("DD-MM-YYYY HH:mm");

        let varInterval = 0;
        let interval = utilis.convertToMillSeconds(firstValue.interval);
        let newList = [];
        newList.push(firstValue)
        for (let i = 0; i < selectedTruckTypes.request.length - 1; i++) {
            varInterval = varInterval + interval;
            let nexDate = getNextDateWithNumber(varInterval, firstValue.dateAndTime);

            let newValue = { ...firstValue, "dateAndTime": moment(nexDate, "DD/MM/YYYY HH:mm").format("DD-MM-YYYY HH:mm") };
            newList.push(newValue)
        }
        setSelectedTruckTypes({ ...selectedTruckTypes, "request": [...newList], "action": "editDate" })

    };

    const doSeacrch = async (value, type) => {
        if (type === "DECLARATION") {
            await setBooking();
            await setContainers()
            let res = await BookingService.getRequestByDeclaration(value)
            if (res && res?.status === "success") {
                await setBoeDetails({ ...boeDetails, "data": res?.data?.dataItems })
                await setSuccess(true)
            } else {
                await setSuccess(false)
            }
        } else if (type === "BOOKING") {
            await setBoeDetails();
            await setContainers()
            let res = await BookingService.getBookingByNumber(value)
            if (res && res?.status === "success") {
                await setSuccess(true)
                await setBooking({ ...booking, "data": res?.data?.dataItems[0] })
            }
            else {
                await setSuccess(false)
            }

        } else if (type === "CONTAINER") {
            await setBooking();
            await setBoeDetails();
            await setSuccess(true);
            await console.log("CONT ", type);
            let res = await BookingService.getContainerRequest(value)
            if (res && res?.status === "success") {
                await setSuccess(true)
                await setContainers({ ...containers, "data": res?.data?.dataItems })
            }
            else {
                await setSuccess(false)
            }

        }
    }
    const landClick = async (serviceNumber, type) => {
        console.log(`${serviceNumber ?? ""} ### ${type ?? ""}`)
        await doSeacrch(serviceNumber, type)
    }
    const handleSearchClick = async (e, value, type) => {
        if (propsHistoryValue?.from) {
            // setPropsHistoryValue(...propsHistoryValue.form = false)
            await setPropsHistoryValue(prevState => ({
                ...prevState,
                [propsHistoryValue.form]: false
            }));
        }
        await doSeacrch(value, type);
    }
    function getStepContent(activeStep) {

        switch (activeStep) {
            case 1:
                return (
                    <></>
                );
            case 2:
                return (
                    <></>
            );
            case 3:
                return (
                    <></>
                );
            case 4:
                return (
                    <></>
                );
            case 5:
                return (
                    <></>
                );
        }
    }
    return (
        <>
            <div className={classes.root}>
                <Paper className={classes.paper}>
                    <Grid container xs={12} sm spacing={1}>
                        {/* <SearchInput defaultValue={propsHistoryValue?.serviceNumber} defaultType={propsHistoryValue?.serviceType} getType={(e) => setInputType(e)} searchClick={(e) => handleSearchClick(e)} ></SearchInput> */}
                        <SearchInput optionValues={serviceOptionTypes} defaultValue={propsHistoryValue?.serviceNumber ?? ""} defaultType={`${propsHistoryValue?.serviceType ?? "DECLARATION"}`} changeValue={(e) => setSearchValue(e)} changeType={(e) => setInputType(e)} searchClick={(e) => handleSearchClick(e, searchValue, inputType)} ></SearchInput>

                    </Grid>
                {success?    (success && <>

                        {boeDetails?.data?.length > 0 && boeDetails?.data &&
                            <>
                                <Grid item container xs={12} sm spacing={1}>
                                    <Grid item xs={12} sm={1} md={1}>
                                        <Box className="booking-boxOnCard" component="button" style={{ cursor: 'pointer' }}  >
                                            <ArrowLeftOutlined />
                                        </Box>
                                    </Grid>
                                    <Grid item xs>
                                        <>
                                            {boeDetails?.data?.length > 1 ?
                                                <Typography variant="h4">{`${boeDetails?.data?.length} Bookings`} </Typography>
                                                : <Typography variant="h4">{`${boeDetails?.data?.length} Booking`} </Typography>}
                                        </>
                                    </Grid>
                                </Grid>


                                <Grid item container xs={12} sm spacing={1}>

                                    <Grid item xs={12} sm={2} md={2}>
                                        <Typography variant="subtitle2">Booking Number</Typography>
                                    </Grid>
                                    <Grid item xs={12} sm={3} md={3} >
                                        <Typography variant="subtitle2">Booked On</Typography>
                                    </Grid>
                                    <Grid item xs={12} sm={2} md={2} >
                                        <Typography variant="subtitle2">Consignee Name</Typography>
                                    </Grid>
                                    <Grid item xs={6} sm={3} md={3} >
                                        <Typography variant="subtitle2">Delivery Order Validity</Typography>
                                    </Grid>
                                    <Grid item xs={12} sm={1} md={1} >
                                        <Typography variant="subtitle2">Containers</Typography>
                                    </Grid>
                                    <Grid item xs={12} sm={1} md={1} >
                                        <Typography variant="button"></Typography>
                                    </Grid>

                                </Grid>
                            </>
                        }
                        {boeDetails?.data?.map((details, id) => (

                            <BookingCard onBookingClick={(e) => { onBookingViewClick(e) }} bookingNumber={details.bookingNumber} containerCount={details.containerCount} deliveryOrderValidity={details.deliveryOrderValidity} bookedOn={details.bookedOn} consigneeName={details.consigneeName} key={id}></BookingCard>

                        ))}

                        {/* Booking  */}

                        {booking?.data?.referenceNumber &&
                            <>
                                <Grid item container xs={12} sm spacing={1}>
                                    <Grid item xs={12} sm={1} md={1}>
                                        <Box className="booking-boxOnCard" component="button" style={{ cursor: 'pointer' }}  >
                                            <ArrowLeftOutlined />
                                        </Box>
                                    </Grid>
                                    <Grid item xs>
                                        <Typography variant="h4">{`Booking ${booking?.data?.referenceNumber}`}</Typography>
                                    </Grid>
                                </Grid>

                                <Grid container xs={12} spacing={1}>
                                    <Grid item xs={6} sm={2} >
                                        <Typography variant="subtitle1">Amount Paid</Typography>
                                        <Typography variant="subtitle2">{booking?.data?.totalAmt} AED</Typography>
                                    </Grid>
                                    <Grid item xs={6} sm={2} >
                                        <Typography variant="subtitle1">No. of Containers</Typography>
                                        <Typography variant="subtitle2">{booking?.data?.containerList?.length}</Typography>
                                    </Grid>
                                    <Grid item xs={6} sm={2} >
                                        <Typography variant="subtitle1">Booked On</Typography>
                                        <Typography variant="subtitle2">{booking?.data?.creationDate}</Typography>
                                    </Grid>
                                    {booking?.data?.requestTruckType && booking?.data?.refStatus?.code == "PEND" &&
                                        <>
                                            <Grid item xs={6} sm={2} >

                                                {/* <Typography variant="subtitle1">No. of Trucks  </Typography> */}

                                                <Button endIcon onClick={() => { handleOpenEdit(booking?.data?.requestTruckType, "addMore") }}> No. of Trucks  <Edit /> </Button>

                                                <Typography variant="subtitle2">{booking?.data?.requestTruckType?.length}</Typography>


                                            </Grid>
                                            <Grid item xs={6} sm={2} >


                                                <Button endIcon onClick={() => { handleOpenEdit(booking?.data?.requestTruckType, "editDate") }}> Date and time<Edit /> </Button>
                                                <Typography variant="subtitle2">{booking?.data?.requestTruckType[0]?.dateAndTime}</Typography>
                                            </Grid>
                                            <Grid item xs={6} sm={2} >

                                                <Button endIcon onClick={() => { handleOpenEdit(booking?.data?.requestTruckType, "editInterval") }}> Time interval<Edit /> </Button>
                                                <Typography variant="subtitle2">{booking?.data?.requestTruckType[0]?.interval}</Typography>
                                            </Grid>
                                        </>
                                    }
                                </Grid>
                            </>
                        }
                        <Grid container xs={12} spacing={1}>
                            {booking?.data?.containerList?.map((container, id) => (
                                <Grid key={id} item container xs={12} spacing={1}>
                                    <ContainerCard
                                        boxNumber={id + 1}
                                        key={id}
                                        declaration={container?.boeNumber}
                                        consigneeName={container?.consigneeDetails}
                                        contactNumber={container?.consigneeContactNumber}
                                        dateAndTime={container?.date_time}
                                        contactPerson={container?.consigneeContactName}
                                        pickUpLocation={container?.pickupLocation}
                                        dropAddress={container?.dropAddress}
                                        dropZone={container?.dropZone}
                                        containerNumber={container?.container_number}
                                        tokenInNumber={container?.tokenIn}
                                        timeInSlot={container?.timeInSlot ? container.timeInSlot: undefined}
                                        tokenOutNumber={container?.tokenOut}
                                        timeOutSlot={container?.timeOutSlot ? container.timeOutSlot: undefined}
                                        fcl_truckNumber={container?.truckNumber}
                                        mt_truckNumber={container?.mtTruck}
                                        truckType={container?.truckType}
                                        order={container?.order}
                                        status={{ "code": container?.refStatus.code, "name": container?.refStatus?.refStatusLocales?.name }}
                                        onDelete={() => handleDelete(container, id, "booking")}
                                        assignTruck={(e) => console.log("ASSIGN STATE ", e)}
                                        openTrackClick={() => onGetTrackList(container, openDrawer(true))}
                                        deliveredClick={() => handleOpenDeliveredDialog(container)}
                                        fclOutClick={(e) => console.log("fcl click ", e)}
                                        mtInClick={(e) => console.log("mtin click ", e)}
                                        onContactNumberChange={(e) => console.log("Contavct change ", e)}
                                        onContactPersonChange={(e) => console.log("number  change  ", e)}
                                        onDropLocationChange={(e) => console.log("Drop change  ", e)}
                                        updateChange={e => updateContainerChange(container)}
                                        getEditContainer={e => updateParentBookingView(id, e)}
                                        downloadDoc={() => handleDownload(container)}
                                    >

                                    </ContainerCard>
                                </Grid>
                            ))}
                        </Grid>

                        {/* Container  */}
                        {containers &&

                            <>
                                {containers?.data?.map((containers, id) => (
                                    <Grid key={id} container xs={12} sm spacing={1}>
                                        <Grid container xs={12} spacing={1}>
                                            <Grid item xs={12} sm={1} md={1}>
                                                <Box className="booking-boxOnCard" style={{ cursor: 'pointer' }}  >
                                                    &lsaquo;
                                                </Box>
                                            </Grid>
                                            <Grid item xs>
                                                <Typography variant="h4">{`Booking ${containers?.requestDetailsNumber}`}</Typography>
                                            </Grid>
                                        </Grid>
                                        <Grid container xs={12} spacing={1}>
                                            <ContainerCard
                                                boxNumber={1}
                                                declaration={containers?.boeNumber}
                                                consigneeName={containers?.consigneeDetails}
                                                contactNumber={containers?.consigneeContactNumber}
                                                dateAndTime={containers?.date_time}
                                                contactPerson={containers?.consigneeContactName}
                                                pickUpLocation={containers?.pickupLocation}
                                                dropAddress={containers?.dropAddress}
                                                dropZone={containers?.dropZone}
                                                containerNumber={containers?.container_number}
                                                tokenInNumber={containers?.tokenIn}
                                                order={containers?.order}
                                                timeInSlot={containers?.timeInSlot ? containers.timeInSlot: undefined}
                                                tokenOutNumber={containers?.tokenOut}
                                                timeOutSlot={containers?.timeOutSlot ? containers.timeOutSlot: undefined}
                                                fcl_truckNumber={containers?.truckNumber}
                                                mt_truckNumber={containers?.mtTruck}
                                                truckType={containers?.truckType}
                                                status={{ "code": containers?.refStatus?.code, "name": containers?.refStatus?.refStatusLocales?.name }}
                                                onDelete={() => handleDelete(containers, id, "container")}
                                                assignTruck={(e) => console.log("ASSIGN STATE ", e)}
                                                downloadDoc={() => handleDownload(containers)}
                                                openTrackClick={() => onGetTrackList(containers, openDrawer(true))}
                                                deliveredClick={() => handleOpenDeliveredDialog(containers)}
                                                fclOutClick={(e) => console.log("fcl click ", e)}
                                                mtInClick={(e) => console.log("mtin click ", e)}
                                                onContactNumberChange={(e) => console.log("Contavct change ", e)}
                                                onContactPersonChange={(e) => console.log("number  change  ", e)}
                                                onDropLocationChange={(e) => console.log("Drop change  ", e)}
                                                updateChange={e => updateContainerChange(containers)}
                                                getEditContainer={e => updateContainerView(id, e)}
                                            >

                                            </ContainerCard>
                                        </Grid>
                                    </Grid>
                                ))}
                            </>
                        }
                    </>
                ):(
                    <Paper
                      elevation={5}
                      style={{
                        borderRadius: 8,
                        padding: "30px",
                        marginTop: 20,
                        minWidth: "760px",
                        minHeight: "100px",
                        color: "#FF7171",
                      }}
                    >
                      <Grid container direction="row" spacing={5}>
                        <Grid item sm={12} xs={12}>
                          <Typography
                            variant="subtitle1"
                            style={{ fontSize: "16px", textAlign: "center" }}
                          >
                            <b>{"No records found"}</b>
                          </Typography>
                        </Grid>
                      </Grid>
                    </Paper>
                  )}
                </Paper>
                <div>
                    <Dialog open={openEdit} selectedValue={selectedTruckTypes} onClose={() => handleEditClose} aria-labelledby="form-dialog-title">
                        <DialogContent>
                            <DialogContentText variant="h5" >{`Total Number of trucks Booked :  ${selectedTruckTypes?.request?.length ?? 0}`}</DialogContentText>
                            <Grid container xs={12} spacing={1}>
                                {selectedTruckTypes?.action === "editDate" && <Grid item xs={12} sm md>
                                    <MuiPickersUtilsProvider utils={DateFnsUtils} >
                                        <KeyboardDateTimePicker
                                            InputAdornmentProps={{ position: "start" }}


                                            autoOk
                                            ampm={false}
                                            variant="inline"
                                            margin="normal"
                                            id="date-picker-inline"
                                            defaultValue={new Date()}
                                            value={selectedDate}
                                            format="dd-MM-yyyy HH:mm"
                                            onChange={(e) => handleDateChange(e)}
                                            KeyboardButtonProps={{
                                                style: {
                                                    fontSize: "12px",
                                                    color: "#1FA5FF",
                                                    padding: 0,
                                                },
                                            }}
                                        />
                                    </MuiPickersUtilsProvider>
                                </Grid>}
                                {selectedTruckTypes?.action === "editInterval" && <Grid item xs={12} sm md>
                                    <Select
                                        labelId="demo-simple-select-helper-label"
                                        id="interval"
                                        value={interval}
                                        defaultValue={selectedTruckTypes?.request[0]?.interval ?? ""}
                                        onChange={(e) => onIntervalChange(e)}
                                        name="interval"
                                        label="Interval"
                                    >
                                        {intervals?.map((item) => (
                                            <MenuItem value={item?.value}>{item?.label}</MenuItem>
                                        ))}
                                    </Select>
                                </Grid>}
                                {selectedTruckTypes?.request?.map((item, id) => (

                                    <Grid key={id} container xs={12} spacing={1}>
                                        <TruckTypeCard dateAndTime={item?.dateAndTime} interval={item?.interval} key={id} truckType={{ label: item?.vehicleName, value: item?.vehicleCode }} changeInterval={(e) => console.log("HMMMM ", e)}></TruckTypeCard>
                                    </Grid>
                                ))}
                                <Grid container xs={12} spacing={1}>
                                    {selectedTruckTypes?.action === "addMore" && <Grid item xs={12} sm={4} md={4}>
                                        <Button onClick={() => addMore()}>Add more</Button>
                                    </Grid>}
                                </Grid>

                            </Grid>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={() => handleEditClose()} color="primary"> Cancel </Button>
                            <Button onClick={() => save()} color="primary"> Save  </Button>
                        </DialogActions>
                    </Dialog>
                    {/* selectedValue={selectedTruckTypes} */}
                    {/* <Dialog open={openDeliveredDialog}  onClose={() => handleOpenDeliveredDialog} aria-labelledby="form-doc-dialog">
                        <DialogContent>
                            <DialogContentText variant="h5" >{`Upload Document `}</DialogContentText>
                           
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={() => handleCloseDeliveredDialog()} color="primary"> Cancel </Button>
                            <Button onClick={() => console.log("save ....")} color="primary"> Save  </Button>
                        </DialogActions>
                    </Dialog> */}
                    {openDeliveredDialog && <UploadDocumentPopup popUpParams={uploadParam} onClose={() => handleCloseDeliveredDialog()} />}
                </div>
                {/* onOpen={openDrawer(true)} */}
                <SwipeableDrawer defaultValue={trackDetails} anchor={"right"} open={openTrack} onOpen={() => openDrawer(true)} onClose={() => openDrawer(false)}>

                    
                        {/* <Paper>
                            <Grid container xs={12} >
                                <Grid item container xs={10} justify="center" alignItems="flex-start" >
                                    <Typography align="justify" variant="subtitle1" noWrap>
                                        {`Track Details For ${trackDetails?.containerNo}`}
                                    </Typography>
                                </Grid>
                                <Grid item container xs={2} justify="flex-end" alignItems="flex-start" >
                                   
                                    <Close fontSize="small" className="red cursor-pointer" onClick={() => closeDrawer()} />
                                    
                                </Grid>
                            </Grid>
                        </Paper> */}
                         <div
          style={{width:"600px"}}

        >
            <div >
                    <CloseIcon fontSize="large" className="red rightAlign" onClick={() => {
                        closeDrawer();
                    }}/>
            </div>
            <br></br>
            <InputLabel
                style={{ fontSize: "20px", color: "#434343", marginLeft: "26px" }}
            >
                {" "}
          Tracking Details for {trackDetails?.containerNo}
                        
        </InputLabel>
                
            <br></br>
            <Divider></Divider>
                            <br></br>
                           
                        {/* <Track tab={"Pending"} steps={trackDetails?.tracks}></Track> */}

                        <Stepper orientation="vertical"
                            activeStep={activeStep}
                            connector={<TestConnector />}
                        >

                            {getSteps(trackDetails?.tracks,trackDetails?.bookingDate)?.map((item, index) => (

                                <Step key={index}>
                                    <StepLabel
                                        StepIconProps={{
                                            classes: {
                                                active: classes.icon,
                                                completed: classes.icon,
                                            },
                                        }}
                                    >{item ?? ""}</StepLabel>
                                    <StepContent>
                                        {/* <div className={classes.actionsContainer}>
                                            <div>
                                                <Button
                                                    disabled={false}
                                                    className={classes.button}
                                                >
                                                    Back
                                                </Button>
                                                <Button
                                                    variant="contained"
                                                    color="primary"
                                                    className={classes.button}
                                                >

                                                </Button>
                                            </div>
                                        </div> */}
                                    </StepContent>
                                </Step>
                            ))}
                        </Stepper>

                        <div>
                            <Typography component={"div"} className={classes.instructions}>
                                {getStepContent(activeStep)}
                            </Typography>
                        </div>
                    </div>
                </SwipeableDrawer>


            </div>
        </>
    );


}


{/* <Timeline align="left">
                            {trackDetails?.tracks?.map((item, id) => (
                                <TimelineItem key={id}>
                                    <TimelineSeparator>
                                        <TimelineDot />
                                        <TimelineConnector />
                                    </TimelineSeparator>
                                    <TimelineContent>
                                        <Paper elevation={3} className={classes.paper}>
                                            <Typography variant="h6" component="h1">
                                                {`${item?.actionDateAndTime}`}
                                            </Typography>
                                            <Typography>{`${item?.status.split("#")[1]}`}</Typography>
                                        </Paper>
                                    </TimelineContent> </TimelineItem>
                            ))}
                        </Timeline> */}